import React from "react";

import H2 from "./h2.js";
import H3 from "./h3.js";
import Text from "./text.js";

class Project extends React.Component {
  constructor() {
    super()
    this.state = { isClicked: false  }
    this.toggleClick = this.toggleClick.bind(this);
  }

  toggleClick() {
    this.setState({isClicked: !(this.state.isClicked)})
  }

  render() {
    return (
      <div style={{ margin: '1em 0', cursor: 'pointer' }} onClick={this.toggleClick}>
        <H2 bold>{this.props.title}</H2> { this.props.course ? "—" : "" } <H2 italic>{this.props.course}</H2> <br />
        <H2 italic>{this.props.team}</H2> { this.props.team ? <br /> : "" }
        { this.state.isClicked ? (<Text dangerouslySetInnerHTML={{ __html: this.props.html }} />) : (<span><Text><p>{this.props.excerpt}</p></Text></span>) }
      </div>
    )
  }
}

export default Project;



