import React from "react";
import styled from "styled-components";

import H1 from "../components/h1.js";
import Section from "../components/section.js";

import Experience from "../components/experience.js"
import Education from "../components/education.js"
import Project from "../components/project.js"
import ListItem from "../components/list-item.js"
import Language from "../components/language.js"

const Container = styled.div`
  display: flex;
  height: 500px;

  @media (max-width: 700px) {
    flex-direction: column;
  }
`;

const Content = styled.div`
  flex: 70%;

  &:last-child {
    margin: 0;
  }
`;

const Sidebar = styled.div`
  flex: 30%;

  @media (min-width: 700px){
    padding-left: 25px;
  }
`;

export default ({ data }) => (
  <Container>
    <Content>
      <Section>
        <H1>Experience</H1>
        {data.experiences.edges.map(({ node }, index) =>
          <Experience
            key={ index }
            place={ node.frontmatter.place }
            city={ node.frontmatter.city }
            position={ node.frontmatter.position }
            startDate={ node.frontmatter.startDate }
            endDate={ node.frontmatter.endDate }
            info={ node.html } />
        )}
      </Section>
      <Section>
        <H1>Education</H1>
        {data.education.edges.map(({ node }, index) =>
          <Education
            key={ index }
            school={ node.frontmatter.school }
            city={ node.frontmatter.city }
            degree={ node.frontmatter.degree }
            startDate={ node.frontmatter.startDate }
            endDate={ node.frontmatter.endDate }
            />
        )}
      </Section>
      <Section>
        <H1>Projects</H1>
        {data.projects.edges.map(({ node }, index) =>
          <Project
            key={ index }
            title={ node.frontmatter.title }
            course={ node.frontmatter.course }
            team={ node.frontmatter.team }
            excerpt={ node.excerpt }
            html={ node.html }
            />
        )}
      </Section>
    </Content>
    <Sidebar>
      <Section>
        <H1>Skills</H1>
        {data.skills.edges.map(({ node }, index) =>
          <ListItem
            key={ index }
            title={ node.frontmatter.title }
            />
        )}
      </Section>
      <Section>
        <H1>Languages</H1>
        {data.languages.edges.map(({ node }, index) =>
          <Language
            key={ index }
            title={ node.frontmatter.title }
            level={ node.frontmatter.level }
            />
        )}
      </Section>
      <Section>
        <H1>Hobbies</H1>
        {data.hobbies.edges.map(({ node }, index) =>
          <ListItem
            key={ index }
            title={ node.frontmatter.title }
            />
        )}
      </Section>
      <Section>
        <H1>Communities</H1>
        {data.communities.edges.map(({ node }, index) =>
          <ListItem
            key={ index }
            title={ node.frontmatter.title }
            />
        )}
      </Section>
    </Sidebar>
  </Container>
)

export const query = graphql`
query IndexQuery {
  experiences: allMarkdownRemark(filter: {frontmatter: {type: {eq: "experience"}}}, sort: {fields: [frontmatter___startDate], order: DESC}) {
    edges {
      node {
        frontmatter {
          place
          city
          position
          startDate
          endDate
          type
        }
        html
        excerpt
      }
    }
  }
  education: allMarkdownRemark(filter: {frontmatter: {type: {eq: "education"}}}, sort: {fields: [frontmatter___startDate], order: DESC}) {
    edges {
      node {
        frontmatter {
          school
          degree
          city
          startDate
          endDate
          type
        }
      }
    }
  }
  projects: allMarkdownRemark(filter: {frontmatter: {type: {eq: "project"}}}, sort: {fields: [frontmatter___title], order: ASC}) {
    edges {
      node {
        frontmatter {
          title
          course
          team
        }
        excerpt
        html
      }
    }
  }
  skills: allMarkdownRemark(filter: {frontmatter: {type: {eq: "skill"}}}, sort: {fields: [frontmatter___title], order: ASC}) {
    edges {
      node {
        frontmatter {
          title
        }
      }
    }
  }
  languages: allMarkdownRemark(filter: {frontmatter: {type: {eq: "language"}}}, sort: {fields: [frontmatter___levelInt], order: DESC}) {
    edges {
      node {
        frontmatter {
          title
          level
        }
      }
    }
  }
  hobbies: allMarkdownRemark(filter: {frontmatter: {type: {eq: "hobby"}}}, sort: {fields: [frontmatter___title], order: ASC}) {
    edges {
      node {
        frontmatter {
          title
        }
      }
    }
  }
  communities: allMarkdownRemark(filter: {frontmatter: {type: {eq: "community"}}}, sort: {fields: [frontmatter___title], order: ASC}) {
    edges {
      node {
        frontmatter {
          title
        }
      }
    }
  }
}
`



