import React from "react";
import moment from "moment";
import H2 from "./h2.js";
import H3 from "./h3.js";

export default props => (
  <div style={{ margin: '1em 0' }}>
    <H2 bold>{props.school},</H2> <H2>{props.city}</H2> <br />
    <H2 italic>{props.degree}</H2> <br/>
    <H3>{ moment(props.startDate).format("MMMM YYYY") } - { props.endDate === "current" ? props.endDate : moment(props.endDate).format("MMMM YYYY") }</H3>
  </div>
);



