import styled, { css } from "styled-components";

export default styled.text`
  font-family: Merriweather;
  font-size: 11pt;
  color: black;

  ${props => props.italic && css`
    font-style: italic;
  `}
  ${props => props.bold && css`
    font-weight: bold;
  `}
`;



