import React from "react";
import moment from "moment";

import H2 from "./h2.js";
import H3 from "./h3.js";
import Text from "./text.js";

export default props => (
  <div>
    <H2 bold>{ props.place },</H2> <H2>{ props.city }</H2> — <H2 italic>{ props.position }</H2> <br/>
    <H3>{ moment(props.startDate).format("MMMM YYYY") } - { props.endDate === "current" ? props.endDate : moment(props.endDate).format("MMMM YYYY") }</H3>
    <Text dangerouslySetInnerHTML={{ __html: props.info }} />
  </div>
);



